.cell {
  position: relative;
  width: 40px;
  height: 40px;
  overflow: hidden;
  border: 0.5px solid white;
}

.cell-mask {
  position: absolute;
  top: -18px;
  left: -18px;
  width: 36px;
  height: 36px;
  border: 20px solid #2196f3;
  border-radius: 50%;
}



